import { Component, OnInit, Input, HostBinding, OnChanges } from '@angular/core';
import { DomSanitizer, SafeStyle } from '@angular/platform-browser';

@Component({
  selector: 'app-boat',
  templateUrl: './boat.component.svg',
  styleUrls: ['./boat.component.scss']
})
export class BoatComponent implements OnChanges {

  @Input() angle: string;
  @Input() left: string;
  @Input() top: string;
  @Input() color1: string;
  @Input() color2: string;
  @Input() scale = 1;

  get transform(): SafeStyle {
    return this.sanitazer.bypassSecurityTrustStyle(`${this.angle} scale(${this.scale})`);
  }

  constructor(private sanitazer: DomSanitizer) { }

  ngOnChanges(change) {
  }

}
