export class Boat {
  public angle = '0';
  public top = '0';
  public left = '0';
  public position = 0;
  public velocity = 1;
  public color1 = 'black';
  public color2 = 'red';
  public deepLevel = 0;
  public scale = 1;

  constructor(fields?: {
    position?: number,
    color1?: string,
    color2?: string,
    velocity?: number,
    deepLevel?: number,
    scale?: number
  }) {
    if (fields) {
      Object.assign(this, fields);
    }
  }
}
