import { Component, ViewChild, ElementRef, AfterViewInit, ViewEncapsulation, OnDestroy, HostListener } from '@angular/core';
import { compileNgModule } from '@angular/compiler';
import { Boat } from '../boat/boat';

@Component({
  selector: 'app-canvas',
  templateUrl: './canvas.component.html',
  styleUrls: ['./canvas.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class CanvasComponent implements AfterViewInit, OnDestroy {

  private canvasElement = null;
  private ctx = null;
  private waves = [];
  private wave1 = [];
  private wave2 = [];
  private wave3 = [];
  private screenWidth = 0;
  private screenHeight = 0;
  private numWaves = 7;
  private wavesSize = 5;
  private moveWavesId = null;
  private moveBoatId = null;
  private drawId = null;
  private horizonHeight = 0;

  public boatPosition1 = new Boat({
    position: 200,
    color1: '#ff195a',
    color2: '#cf091a',
    velocity: 0.3,
    deepLevel: 10,
    scale: 0.6
  });
  public boatPosition2 = new Boat({
    position: 20,
    color1: '#5422ed',
    color2: '#1402ad',
    velocity: 0.5,
    deepLevel: 100,
    scale: 1
  });

  @ViewChild('main', { static: false }) main: ElementRef;
  @ViewChild('boat', { static: false }) boat: ElementRef;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.initSizes();
    this.initWaves();
  }

  constructor() {
  }

  ngAfterViewInit() {

    this.initCanvas();
    this.initSizes();
    this.initWaves();
    this.startLoop();

  }

  initCanvas() {
    this.canvasElement = document.createElement('canvas');
    this.ctx = this.canvasElement.getContext('2d');
    this.main.nativeElement.insertBefore(this.canvasElement, this.main.nativeElement.firstChild);
  }

  initSizes() {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;

    this.canvasElement.width = this.screenWidth;
    this.canvasElement.height = this.screenHeight;

    this.numWaves = Math.floor(this.screenWidth / 200);
    this.wavesSize = Math.floor(this.screenHeight / 300);

    this.horizonHeight = this.screenHeight * .3;
  }

  initWaves() {
    const num = this.numWaves;
    const size = this.wavesSize;
    for (let x = 0; x < this.screenWidth; x++) {
      const s1 = (x) * num / this.screenWidth * 2 * Math.PI;
      const s2 = (x + 200) * (num - 1) / this.screenWidth * 2 * Math.PI;
      const s3 = (x + 100) * (num + 1) / this.screenWidth * 2 * Math.PI;
      this.wave1[x] = Math.sin(s1);
      this.wave2[x] = Math.sin(s2);
      this.wave3[x] = Math.sin(s3);
      this.waves[x] = ((this.wave1[x] + this.wave2[x] + this.wave3[x]) * size) + this.horizonHeight;
    }

    requestAnimationFrame(() => { this.draw(); });
  }

  moveBoat(boatPosition: Boat) {
    let y1 = this.waves[Math.floor(boatPosition.position)];
    let y2 = this.waves[Math.floor(boatPosition.position) + 1];

    boatPosition.position += boatPosition.velocity;

    const newPos = Math.floor(boatPosition.position);

    if (newPos > this.screenWidth + 50) {
      boatPosition.position = -50;
    }
    if (boatPosition.position >= this.screenWidth) {
      y1 = this.waves[newPos - this.screenWidth];
      y2 = this.waves[newPos - this.screenWidth + 1];
    }
    if (boatPosition.position < 0) {
      y1 = this.waves[this.screenWidth + newPos];
      y2 = this.waves[this.screenWidth + newPos + 1];
    }

    boatPosition.angle = `rotate(${-Math.atan(y1 - y2)}rad)`;
    boatPosition.left = newPos - 40 + 'px'; // adjust the center of the boat
    boatPosition.top = y1 - 55 + boatPosition.deepLevel + 'px'; // adjust the center of the boat
  }

  moveWaves() {
    const num = this.numWaves;
    const size = this.wavesSize;

    const wave2zero = this.wave2[0];
    const wave3zero = this.wave3[0];

    for (let x = 0; x < this.screenWidth; x++) {
      if (x === this.screenWidth - 1) {
        this.wave2[x] = wave2zero;
        this.wave3[x] = wave3zero;
      } else {
        this.wave2[x] = this.wave2[x + 1];
        this.wave3[x] = this.wave3[x + 1];
      }
      this.waves[x] = ((this.wave1[x] + this.wave2[x] + this.wave3[x]) * size) + this.horizonHeight;
    }
  }

  startLoop() {
    clearInterval(this.moveWavesId);
    this.moveWavesId = setInterval(() => {
      this.moveWaves();
    }, 15);

    clearInterval(this.moveBoatId);
    this.moveBoatId = setInterval(() => {
      this.moveBoat(this.boatPosition1);
      this.moveBoat(this.boatPosition2);
    }, 10);

    clearInterval(this.drawId);
    this.drawId = setInterval(() => {
      requestAnimationFrame(() => { this.draw(); });
    }, 100);
  }

  draw() {
    this.ctx.clearRect(0, 0, this.screenWidth, this.screenHeight);
    this.ctx.beginPath();
    this.ctx.moveTo(0, this.screenHeight);

    for (let x = 0; x < this.screenWidth; x++) {
      this.ctx.lineTo(x, this.waves[x]);
    }

    const gradient = this.ctx.createLinearGradient(0, 0, 0, this.screenHeight);
    gradient.addColorStop(.5, '#2196d3');
    gradient.addColorStop(1, '#000025');
    this.ctx.fillStyle = gradient;
    this.ctx.lineTo(this.screenWidth, this.screenHeight);
    this.ctx.fill();
  }

  ngOnDestroy() {
    clearInterval(this.moveWavesId);
    clearInterval(this.moveBoatId);
    clearInterval(this.drawId);
  }

}
